

@mixin slicknav{
	.slick-slide {
		outline: none
	}
	.slick-arrow{
		background-color: rgba(#fff, 0.5);
		border-radius: 50%;
		z-index: 9;
		height: 60px;
		width: 60px;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: .3s ease;
		overflow: visible;
		font-size: 1.6rem;
		color: #fff !important;
		transform:scale(1);
		@include transition;
		&:hover{
			background-color: rgba(#fff, 0.5);
			
		}
		
		@include responsivemd{
			height: 40px;
			width: 40px;
		}
		svg{
			fill: #fff;
		}
		&:hover{
			opacity: 1;
			
		}
		&:before{
			content: none;
		}
		&.hide{
			opacity: 0;
			pointer-events: none;
			@include transition-in;
		}
		
	}
	.slick-prev{
		left: 25px;
	}
	.slick-next{
		right: 25px;
	}
	.carousel{
		opacity: 0;
		visibility: hidden;
		transition: opacity 1s ease;
		&.slick-initialized{
			visibility: visible;
			opacity: 1;
		}
	}
	.slick-disabled{
		transform:scale(0)
	}
	.item{
		cursor: grab;
		&.grab{
			cursor: grabbing;
		}
	}
	
}

@mixin slickdots{
	.carousel{
		margin-bottom: 0;
	}
	.slick-slide { -webkit-transform: translate3d(0,0,0); }
	.slick-dots{
		bottom: 25px;
		
		
		button{
			// display: none;
			opacity: 0;
			position: absolute;
			height: 100%;
			width: 100%;
			top: 0;
			left:0;
		}
		li{
			height: 4px;
			width: 40px;
			border-radius: 10px;
			background-color: #fff;
			transition: .3s linear;
			transition-delay: .3s;
			// margin: 0;
			position: relative;
			opacity: 0.3;
			&.slick-active{
				opacity: 1;
				width: 85px;
				transition-delay: 0s;
				
			}
		}
	}
	.item.slick-slide.slick-active{
		transform: translateY(0);
		.card__img{
			img{
				opacity: 1;				
			}
		}
	}
	.item{
		transform: translateY(20px);
		transition:.3s linear;
		.card__img{
			background-color:$primary-color;
			img{
				opacity: 0;
				transition:.3s linear;
				
			}
		}
		
	}
}


@mixin active{
	&.active{
		color:#fff;
		background-color: $red;
		@include transition-in;
		
	}
}
@mixin mt{
	margin-top: 30px;
}

@mixin nav__tabs{
	.nav-tabs{
		@include card__margin;
		border-bottom: 2px solid rgba(78, 78, 78, 0.08);
		position: relative;
		.nav-item{
			margin: 0;
			&:first-child{
				.nav-link{
					padding-left: 0;
				}
			}
		}
		.nav-link{
			font-size: 18px;
			border:0 !important;
			padding: 20px;
			padding-top: 10px;
			color: $para;
			position: relative;
			&.active{
				color: $primary-color;
			}
			sup {
				height: 15px;
				width: 15px;
				display: inline-block;
				background-color: #f2f2f2;
				line-height: 15px;
				text-align: center;
				border-radius: 50%;
				font-size: 12px;
			}
		}
		.indicator{
			position: absolute;
			height: 2px;
			width: 114px;
			background-color: $primary-color;
			left: 0;
			bottom: -2px;
			@include transition-in;
			
		}
	}
}

@mixin product--loop{
	.bg--color--change, .svg--color--change svg{
		transition: .3s linear;
	}
	#product1{
		.bg--color--change{
			background:$product1;
		}
		.svg--color--change svg{
			fill:$product1;
		}
	}
	#product2{
		.bg--color--change{
			background:$product2;
		}
		.svg--color--change svg{
			fill:$product2;
		}
	}
	#product3{
