@mixin card{
	.card, .card__horizontal{
		border:0;
		box-shadow: 0;
		font-size: 16px;
		background-color: #fff;
		position: relative;
		z-index: 1;
		padding:30px 27px;
		@include transition-in;
		@include border-radius;
		backface-visibility: hidden;
		will-change: transform;
		-webkit-perspective: 1000;
		@include responsivelg{
			padding: 25px;
		}
		img{
			@include image;
		}
		.card__img{
			height: 135px;
			overflow: hidden;
			margin-bottom: 20px;
			position: relative;
			@include border-radius;
			svg {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				path{
					@include transition-in;
				}
			}
		}
		.card__svg{
			margin: auto;
			margin-bottom: 30px;
			height: 92px;
			width: auto;
			@include responsivemd{
				height: 70px;
				margin-bottom: 20px;
				width: auto;
			}
			svg{
				height: 100%;
			}
		}
		.card__title{
			font-size: 24px;
			line-height: 1.3;
			font-weight: 700;
			margin: 0;
			margin-bottom: 10px;
		}
		.card__body{
			padding: 0;
			
		}
		.small__title{
			color:rgba($para, 0.8);
			display: block;
			font-size: 17px;
			font-style: normal;
			margin-bottom: 8px;
		}
		time{
			color:rgba($para, 0.5);
			font-size: 14px;
		}
		label.live{
			background: #ED1C24;
			position: absolute;
			top: 10px;
			right: 10px;
			color: #fff;
			padding: 6px 12px;
			font-size: 14px;
			line-height: 1.3;
			margin: 0;
			@include border-radius;
		}
		button{
			@include button;
		}
		.para{
			color: rgba(76, 77, 79, 0.8);
			font-size: 16px;
			// line-height: 1.5;
		}
		
		&:hover{
			@include box-shadow;
			transform: scale(1.03) translateZ(0);
			@include transition-out;
			@include responsivelg{
				transform: none;
				
			}
			.card__img{
				svg{
					path{
						fill-opacity:1;
					}
				}
			}
		}
		
	}
	
}



@mixin article{
	.row{
		margin: 0 -15px;
		margin-bottom: 15px;
		@include responsivemd{
			margin: 0 -10px;
			margin-bottom: 10px;
		}
		&> article{
			padding: 0 15px;
			@include responsivemd{
				padding: 0 10px;
			}
		}
	}
}
.price {
	color:#ff4c4c;
	font-weight: 700;
}
.title__wrapper{
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;
	.section__title{
		margin-bottom: 0 !important;
	}
	
}

@mixin card-hr{
	.card__hr{
		flex-direction: row;
		.card__img{
			height: 150px;
			overflow: hidden;
			width: 150px;
			border-radius: 50%;
			margin-right: 2em;
			@include responsivemmd{
				height: 100px;
				width: 100px;
				margin-right: 1em;
			}
			@include responsive(360px){
				height: 70px;
				width: 70px;
				margin-right: 10px;
			}
			
			
		}
		.card__title{
			margin: 0 !important;
			width: 240px;
			@include responsivemd{
				width: 100%;
			}
		}
		.price{
			margin: 0 !important;
		}
		.card__body {
			text-align: left;
			display: flex;
			flex-grow: 2;
			justify-content: space-between;
			align-items: center;
			width: calc(100% - 100px);
			
			@include responsivemd{
				flex-wrap: wrap;
				
			}
			@include responsive(360px){
				font-size: 12px;
			}
			.qty{
				@include responsivemd{
					margin-right: 0;
				}
			}
			.qty button, .qty .form-control{
				@include responsivemd{
					width: 50px;
					height: 50px;
					
				}
				@include responsivesm{
					width: 30px;
					height: 30px;
					line-height: 1;
				}
			}
		}
		i{
			vertical-align: middle;
			columns: $lite;
		}
	}
}

.para{
	line-height: 1.5;
	font-size: 18px;
	margin-bottom: 0;
	color: $para;
	@include responsivemd{
		font-size: 16px;	
	}
	@include responsivesm{
		font-size: 14px;
	}
	
}
.text__active{
	color: $primary-color;
}
.text-muted{
	color: #A5A5A5 !important;
}

@mixin dur{
	.duration{
		background-color: $para;
		color: #fff;
		position: absolute;
		right: 0;
		bottom:0;
		font-size: 13px;
		border-radius:5px 0 0 0;
		padding: 0px 8px;
		line-height: 1.6;
	}
}

@mixin modal{
	
	padding: 0;
	.modal-content{
		border-radius:20px;
		border:0;
	}
	.modal-body{
		padding: 60px;
		position: relative;
		text-align: center;
		
		button{
			@include button;
		}
		.modal-title{
			margin-bottom: 20px;
		}
	}
	.close{
		position: absolute;
		top:1em;
		right: 1em;
		z-index: 2;
	}
	.modal-header{
		border:0;
		padding: 0;
	}
}



@mixin cardhr--scroll{
	.row{
		@include responsivelg{
			@include scrollbar;
		}
		&> article {
			@include responsivelg{
				max-width: 100%;
				flex-basis: 30%;
				scroll-snap-align: end;
			}
			@include responsivemd{
				flex-basis: 40%;
			}
		}
		.card{
			@include responsivelg{
				white-space: normal;
			}
		}
	}
}

.toast{
	position: fixed;
	bottom: 10px;
	left: 10px;
	padding: 10px;
	border-radius: 20px;
	background-color:#e35d6a;
	&.success{
		background-color: #53b866;
	}
	.close{
		color: #fff;
		margin-bottom: 0 !important;
	}
}



