@import './component_css/header';

body{
	background-color:#fff;
	margin: 0;
	padding: 0;
	
}

@include product--loop;



.screen{
	height: 100vh;
	width: 100vw;
	overflow: hidden;
	position: relative;
	.maincomponent{
		background-color:$primary-color;
		height: 60vh;
		opacity: 0.9;
		clip-path:polygon(0% 100%, 0% 0%, 30.42% 12.7vh, 48.89% 16.11vh, 74.74% 12.28vh, 100% 0%, 100% 100%);
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		z-index: -1;
		transition:.3s ease;	
	}
}

.body--back{
	.svg--circular{
		position: absolute;
		height: 100%;
		width: 680vh;
		top: 50%;
		left: 50%;
		transform: translate(-50.5%, -638vh);
		z-index: 0;
		svg{
			width: 100%;
			height: auto;
			fill:#4f3d65;
			overflow: visible;
		}
		
	}
}

.product{
	@include slicknav;
	@include slickdots;
	img{
		@include image;
		object-fit:contain;
		padding: 12px;
		max-width: 70vw;
		transform: rotate(26deg) translate(-10vw, -12vh);
		transition:.3s linear;
		transform-origin: center;
	}
	
	.card--img{
		height: 60vh;
	}
	.slick-slider{
		// height: 100vh;
	}
	.item{
		position: relative;
		height: 100vh;
		display: flex !important;
		align-items: center;
		justify-content: center;
		// width: 50vw;
	}
	
	// curve
	.slick-active{
		img{
			transform: rotate(0deg) translate(0vw, 0vh);
			// transform-origin: 100% 0;
			
			
			
		}
		& ~ .slick-slide{
			img{
				transform: rotate(-26deg);
				// transform-origin: -80vh 0px;
				
			}
		}
		
		& + .slick-slide{
			img{
				transform: rotate(-20deg) translate(10vw, -12vh);
				
			}
		}
	}
}

@import './component_css/footer';




