@import '../custom';
@import './component';

.form-control[readonly] {
	background-color: transparent;
}


// main menu
header{	
	position: fixed;
	z-index: 99;
	font-size: 16px;
	left: 0;
	top: 0;
	width: 100%;
	.nav{
		align-items: center;
		padding: 0;

	}
	.grow{
		flex-grow: 2;
	}
	.section__rule{
		padding: 20px 0;
	}
	.logo svg{
		fill:$primary-color;
		height: 40px ;
		width:auto;
	}
	
	button{
		@include button;
		&.active{
			color: $dark;
			background-color: #fff;
			&:hover{
				box-shadow: rgba($dark, 0.05) 0px 20px 20px;
				color: $primary-color;

			}
		}
		&.sign-in{
			padding-left: 18px;
			padding-right: 18px;
		}	
	}
	
}



.subscription-modal{
	@include modal;
}
