.mobile{
	// background-color:#fff;
	.text__wrapper{
		padding: 80px 0;
		padding-right: 0;
		@include responsivelg{
			padding: 0;
			padding-right: 40px;
		}
		.d-flex{
			margin-top: 2em;
			a{
				// margin-right: 16px;
				@include transition-out;
				&:hover{
					transform: scale(1.03);
					@include box-shadow;
				}
			}
		}

	}
	.card__img{
		height: 430px;
		width: 100%;
		display: block;
		position: relative;
		@include responsivelg{
			height: 300px;
		}
		img{
			@include image;
			object-fit: contain;
		}
	}
	.app{
		justify-content: space-between;
		img{
			height: 40px;
			width: auto;
			object-fit: contain;
			margin: 8px;
			margin-left: 0;
		}
		.left{
			// margin-right: 30px;
		}
	}
}


.subcribe{
	background-color: #3F3F3F;
	padding: 40px;
	@include border-radius;
	@include responsivelg{
		padding: 30px;
	}
	@include responsivemd{
		padding: 20px;
	}

	.section__title{
		color: #fff;
		font-size: 30px;
		margin-bottom: 10px;
	}

	.d-flex{
		align-items: center;
		justify-content: center;
	}

	img{
		height: 90px;
		width: auto;
		object-fit: contain;
		@include responsivemd{
			height: 60px;
		}
	}
	.text__wrapper {
		margin: 0 40px 0 30px;
		min-width: 31%;
		width: 600px;
		@include responsivelg{
			margin: 0 10px 0 30px;
		}
		@include responsivemd{
			margin-left: 15px;
		}
	}

	.form-inline{
		position: relative;
		width: 350px;
		@include responsivelg{
			flex-wrap: nowrap;
		}
		.form-group{
			flex-grow: 2;
			@include responsivelg{
				flex: 0 0 0;
				flex-grow: 1;
			}
		}
		.form-control{
			background-color: $para;
			color: #fff;
			width: 100%;
			border: 0;
			box-shadow: none;
			outline: none;
			height: 56px;
			border-radius: 10px 0 0 10px; 
			border-bottom: 1px solid transparent;
			&::placeholder{
				color: #a7a7a7;
				font-weight: 600;
			}
			&:focus{
				border-bottom-color:   $primary-color;
			}
		}
		button{
			@include button;
			height: 56px;
			width: 59px;
			padding: 0;
			border-radius:0 10px  10px 0; 
			transform: none !important;
			@include responsivemd{
				width: 50px;
			}
			svg{
				transition: .3s ease;
			}
			&:hover{
				svg{
					transform: translateX(20px);
				}
			}
		}
	}
}

footer{
	background-color: $para;
	font-weight: 400;
	&> .section__rule{
		padding: 0 !important;
		padding-top: 50px !important;
	}
	.para{
		color: #C4C4C4;
	}
	.footer__title{
		font-size: 26px;
		line-height: 31px;
		color: #fff;font-weight: bold;
		margin-bottom: 14px;
	}
	.main__footer{
		padding: 80px 0 30px 0;

		@include article;
		@include responsivemd{
			padding-top: 30px;
			padding-bottom: 20px;
		}
		.row{
			@include responsivemd{
				width: 100%;
				justify-content: flex-end;
			}
		}
		article:first-child{
			padding-right: 10%;
			@include responsivemd{
				padding-right: 0;
				margin-bottom: 20px;
			}
		}
		article:nth-child(2){
			min-width: 20%;
			margin-left: -4%;
		}
	}
	.logo{
		margin-bottom: 20px;
		&+ .para{
			// margin-bottom: 40px;
		}
	}
	ul.para{
		@include ul;
		a{
			padding: 5px 0;

			&.active{
				color: $primary-color;
			}
		}
	}
	.social{
		flex-wrap: wrap;
		a{
			padding: 0 16px 16px 0;
			&:hover{
				path{
					fill: $primary-color;
				}
			}
			&:last-child{
				padding-right: 0;
			}
		}
	}
	
}

.last__footer{
	background-color: #3f3f3f;
	.section__rule{
		padding: 50px 0;
		@include responsivelg{

		}
	} 
	@include article;
	
	.app{
		justify-content: space-between;
		img{
			height: 37px;
			width: auto;
			object-fit: contain;
			margin: 8px;
			margin-left: 0;
		}
		.left{
			// margin-right: 30px;
		}
	}
}

.copyright{
	background-color: #3f3f3f;

	.section__rule{
		padding: 30px 0;
		@include responsivelg{
			padding: 20px 0;	
		}
	}
	.para{
		font-size: 17px;
		@include responsivemd{
			font-size: 14px;
		}
	}
}

